.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.partyFlag {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
}

.partyFlag.small {
  width: 24px;
  height: 24px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background: url(images/flag-none.svg);
}

.partyFlag.NP {
  background: url(images/flag-np.svg);
}

.partyFlag.NSU {
  background: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background: url(images/flag-tsu.svg);
}

.wrap {
  display: inline-block;
}

.optionImg {
  width: 40px;
  float: left;
  margin-right: 3px;
}

.optionItem:hover .optionImg, .optionImg:hover, .optionImgActive {
  opacity: 1;
}

.optionItem, .optionItem:visited {
  display: inline-block;
  cursor: pointer;
  margin: 0 2px -2px 2px;
  text-decoration: none;
  color: #0f0f0f;
  vertical-align: top;
  padding: 0 4px 4px 0;
  border-bottom: 3px solid;
}

@media all and (min-width: 500px) {
  .optionItem, .optionItem:visited {
    margin: 0 10px -2px 10px;
  }
}

.optionText {
  display: inline-block;
  vertical-align: top;
  padding-top: 12px;
}

.optionItemInactive, .optionItemInactive:visited {
  opacity: 0.25;
  border-bottom: none;
}

.optionItem:hover, .optionItemActive, .optionItemActive:visited, .optionItemInactive:hover {
  opacity: 1;
}

.invisible {
  display: block;
  max-height: 0;
  overflow: hidden;
}



