.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.partyFlag {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
}

.partyFlag.small {
  width: 24px;
  height: 24px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background: url(images/flag-none.svg);
}

.partyFlag.NP {
  background: url(images/flag-np.svg);
}

.partyFlag.NSU {
  background: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background: url(images/flag-tsu.svg);
}

.wrap {
  position: relative;
  background: #e6e6e6;
  min-height: 500px;
  text-align: center;
}

.issueTitle {
  padding: 40px 0 0 0;
  font-weight: 700;
  font-size: 30px;
}

.issueStatement {
  color: #3f3f3f;
  background: #dddddd;
  padding: 16px 0;
}

.issueController {
  margin: 40px 0 0 0;
  border-bottom: 1px solid silver;
}

@media all and (min-width: 800px) {
  .issueBlock {
    width: auto;
  }
}

.records {
  margin: 60px auto;
  position: relative;
  text-align: center;
  padding-bottom: 100px;
}

.link, .link:visited {
  color: #b88234;
  text-decoration: none;
  display: inline-block;
  padding: 10px;
}

.link:hover {
  color: #b88234;
  text-decoration: underline;
}

.moreInfo {
  background: white;
  line-height: 2;
  text-align: left;
  padding-bottom: 50px;
}

.moreInfoInner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 10px;
  font-size: 22px;
  color: #353736;
}

.linkButton, .linkButton:visited {
  color: #D6A051;
  border: 2px solid #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}

.linkButton:hover {
  background: #D6A051;
  color: white;
}

.comingButton {
  color: #D6A051;
  border: 2px dotted #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}



