@charset "UTF-8";
.wrap {
  display: inline-block;
  text-align: left;
  max-width: 100%;
}

.header {
  display: block;
  max-width: 100vw;
  padding-right: 20px;
  text-align: center;
  white-space: pre;
}

@media all and (min-width: 800px) {
  .header {
    padding-right: 0px;
  }
}

.userPositionBlock {
  position: relative;
  text-align: center;
}

.userPositionText {
  padding: 6px 10px;
  border: 1px solid gray;
  margin: 0 auto 10px auto;
  display: inline-block;
  position: relative;
}

.userPositionText:before {
  content: '';
  border-top: 10px solid #171717;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  position: absolute;
  bottom: -10px;
  left: calc(50% - 5px);
}

.userPositionText:after {
  content: '';
  border-top: 18px solid #eeeeee;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  position: absolute;
  bottom: -9px;
  left: calc(50% - 9px);
}

.avatarItem {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.avatarImg {
  width: 42px;
  height: 42px;
  overflow: hidden;
  margin: 4px;
  border-radius: 50%;
  border-width: 4px;
  border-style: solid;
  display: inline-block;
  cursor: pointer;
}

.avatarImg:hover, .avatarImgActive {
  box-shadow: 0 0 0 4px #b4b4b4;
}

.KMT {
  border-color: #000095;
}

.DPP {
  border-color: #009A00;
}

.PFP {
  border-color: #FE6407;
}

.TSU {
  border-color: #A25B09;
}

.NP, .NONE {
  border-color: gray;
}

.NSU {
  border-color: rgba(194, 15, 81, 0.9);
}

/* hover 上的時候顯示的文字 */
.activeCube {
  position: absolute;
  z-index: 10;
  display: block;
  top: -60px;
  left: -35px;
  width: 120px;
  min-height: 54px;
  line-height: 1.4;
  text-align: center;
  color: black;
  background: white;
  padding: 8px;
  font-size: 14px;
  border: 2px solid #505050;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
  cursor: pointer;
}



