.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.partyFlag {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
}

.partyFlag.small {
  width: 24px;
  height: 24px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background: url(images/flag-none.svg);
}

.partyFlag.NP {
  background: url(images/flag-np.svg);
}

.partyFlag.NSU {
  background: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background: url(images/flag-tsu.svg);
}

.appbar {
  position: absolute;
  top: 0;
  left: 0;
  background: #f5f5f5;
  padding: 0px;
  height: 52px;
  width: 100%;
  box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
  z-index: 99;
}

.appbar.showMenu {
  height: auto;
}

.inner {
  max-width: 1200px;
  position: relative;
  margin: 0 auto;
}

.siteLogo {
  display: block;
  height: 40px;
  padding: 0;
  margin: 8px;
}

.lists {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 0px;
  overflow: hidden;
  list-style-type: none;
  text-align: left;
}

.lists.showMenu {
  width: 100%;
  max-height: 1000px;
}

.lists li {
  display: inline-block;
  margin: 0;
  width: 50%;
  text-align: center;
}

.navItem, .navItem:visited {
  display: block;
  color: #0f0f0f;
  text-decoration: none;
  padding: 11px;
  height: 54px;
}

.navItem:hover, .navItem.active {
  background: rgba(0, 255, 255, 0.25);
}

.navItem > * {
  display: inline-block;
  vertical-align: middle;
}

.rightToggle {
  position: absolute;
  top: -10px;
  right: 0;
  width: 54px;
  height: 54px;
  cursor: pointer;
}

.icon {
  display: inline-block;
  margin-right: 3px;
}

@media all and (min-width: 400px) {
  .lists li {
    width: 20%;
  }
  .navItem {
    height: auto;
    padding: 11px 5px;
  }
  .navItem > * {
    display: block;
    margin: 0 auto;
  }
  .navItem > span {
    margin-top: 2px;
  }
}

@media screen and (min-width: 540px) {
  .navItem > * {
    display: inline-block;
  }
}

@media screen and (min-width: 720px) {
  .appbar, .appbar.showMenu {
    height: 54px;
  }
  .lists {
    max-height: 1000px;
    display: block;
    list-style-type: none;
    margin: 0 auto;
    text-align: right;
  }
  .lists li {
    margin: 0;
    width: auto;
    display: inline-block;
  }
  .navItem {
    display: inline-block;
    padding: 11px;
  }
  .navItem > * {
    display: inline-block;
    margin: 0;
  }
  .rightToggle {
    display: none;
  }
}



