@charset "UTF-8";
.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

.aye {
  background: #00FFB0;
}

.nay {
  background: #FF0055;
}

.unknown {
  background: #FFF800;
}

.partyFlag {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  vertical-align: top;
  display: inline-block;
}

.partyFlag.small {
  width: 24px;
  height: 24px;
}

.partyFlag.large {
  width: 74px;
  height: 74px;
}

.partyFlag.KMT {
  background: url(images/flag-kmt.svg);
}

.partyFlag.DPP {
  background: url(images/flag-dpp.svg);
}

.partyFlag.GSD {
  background: url(images/flag-gsd.svg);
}

.partyFlag.MKT {
  background: url(images/flag-mkt.svg);
}

.partyFlag.NONE {
  background: url(images/flag-none.svg);
}

.partyFlag.NP {
  background: url(images/flag-np.svg);
}

.partyFlag.NSU {
  background: url(images/flag-npp.svg);
}

.partyFlag.PFP {
  background: url(images/flag-pfp.svg);
}

.partyFlag.TSU {
  background: url(images/flag-tsu.svg);
}

.wrap {
  max-width: 600px;
  margin: 0px 0 100px 0;
  text-align: center;
}

.exploreBelow {
  padding: 20px;
}

.form {
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
  background: #fff;
  text-align: left;
}

@media all and (min-width: 600px) {
  .wrap {
    margin: 40px auto 0;
  }
  .form {
    border: 2px solid #000;
  }
}

.issueRow {
  border-bottom: 2px solid #000;
  text-align: center;
}

.issueName {
  background: #000;
  color: #fff;
  padding: 10px 20px;
}

.issueQuestion {
  padding: 10px 20px;
}

.content {
  padding: 20px;
}

.peopleRow {
  padding: 10px 20px;
  min-height: 100px;
}

.judegementRow {
  border-top: 2px solid #000;
  padding: 10px 20px;
}

.recordRow {
  border-top: 2px solid #000;
}

.clarifyRow {
  position: relative;
  border-top: 2px solid #000;
}

.recordRow > .content > *:not(:first-child),
.clarifyRow > .content > *:not(:first-child) {
  margin-top: 20px;
}

.positionCube {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin: 0 6px;
}

.avatar {
  text-decoration: none;
  width: 120px;
  height: 120px;
  margin: 0 auto;
  border: 4px solid #ddd;
  border-radius: 50%;
  background: #fff;
}

.profileBlock {
  display: block;
  text-align: center;
  margin: 4px 0;
}

@media all and (min-width: 600px) {
  .avatar, .avatar:visited, .profileBlock {
    display: inline-block;
    vertical-align: middle;
  }
  .profileBlock {
    max-width: calc(100% - 140px);
    margin-left: 20px;
    text-align: left;
  }
}

.isCurrent, .isCandidate {
  font-size: 14px;
  line-height: 1.5;
  color: #555;
}

.avatarName {
  vertical-align: top;
}

.avatarName > * {
  display: inline-block;
  vertical-align: middle;
}

.avatarName .name {
  font-size: 40px;
  font-weight: bold;
}

.avatarName .party {
  margin: 0 4px;
}

.avatarName .party > * {
  display: inline-block;
  vertical-align: middle;
}

.avatarName .party .partyName {
  margin-left: 4px;
  font-size: 24px;
}

.judegementRow > * {
  display: inline-block;
  vertical-align: middle;
}

.contentMeta {
  line-height: 1.5;
}

.contentMeta > * {
  display: inline-block;
  margin-right: 16px;
}

.date {
  font-weight: bold;
}

.contentMain {
  margin-top: 20px;
  line-height: 1.5;
  quotes: "“" "”" "‘" "’";
}

.contentMain:before, .contentMain:after {
  display: inline-block;
  color: #ddd;
  font-size: 64px;
  vertical-align: top;
  height: 16px;
  margin-top: -24px;
}

.contentMain:before {
  content: open-quote;
  margin-right: 2px;
}

.contentMain:after {
  content: close-quote;
  margin-left: 2px;
}

.partyThen {
  color: #aaa;
}

.lyURL {
  display: inline-block;
  margin-right: 16px;
}

.clarifyRow > .content {
  padding-top: 36px;
}

.clarifyTitle {
  position: absolute;
  top: 0;
  left: 0;
  background: black;
  color: white;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 0px;
  font-size: 16px;
}

.clarificationContent {
  line-height: 1.5;
}

.clarifyPrompt {
  display: inline-block;
  line-height: 1.5;
}

.seeOtherIssue {
  padding: 10px;
  margin-top: 40px;
  font-size: 20px;
}



